h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.body {
  font-family: var(--main-font-family);
}

:root {
  --primary-text-color: #000;
  --background-header-color: linear-gradient(90deg, #fff 0%, #fafcfa 15%, #e8fee7 20%, #71f96f 66%, rgba(0, 255, 93, .94) 100%);
  --main-font-family: Noto Serif, serif;
}

.visually_hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 1;
  visibility: visible;
  background: rgba(0, 0, 0, .2);
  transition: opacity .5s, visibility .5s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.header {
  width: 1280px;
  background: var(--background-header-color);
  z-index: 1;
  font-family: var(--main-font-family);
  margin: auto;
  padding-top: 12px;
  position: fixed;
  left: 0;
  right: 0;
}

@media screen and (min-width: 768px) {
  .header {
    padding-top: 20px;
  }
}

@media screen and (min-width: 1200px) {
  .header {
    padding-top: 12px;
  }
}

.container.header-container {
  text-align: center;
  justify-content: space-around;
  align-items: center;
  padding-bottom: 5px;
  display: flex;
  position: relative;
}

.nav-list {
  justify-content: space-between;
  gap: 30px;
  display: flex;
}

.nav-item a {
  color: var(--primary-text-color);
  font-size: 20px;
  font-weight: 600;
}

.logo {
  width: 150px;
}

a {
  text-decoration: none;
}

.gallery {
  width: 1280px;
  text-align: center;
  align-items: center;
  margin: auto;
  padding-top: 200px;
}

.gallery-list {
  flex-direction: column;
  gap: 30px;
  display: flex;
}

.gallery-item:hover, .gallery-item:focus {
  border: 1px solid #00f;
}

.gallery-item {
  padding: 30px;
}

.gallery-img {
  width: 80%;
}

.gallery-title {
  text-align: center;
  align-items: center;
  margin: auto auto 30px;
}

.main {
  width: 1280px;
  font-family: var(--main-font-family);
  text-align: center;
  align-items: center;
  padding-top: 200px;
  line-height: 30px;
}

.title {
  margin-bottom: 20px;
}

.about {
  margin-bottom: 20px;
  font-style: italic;
}

.text {
  width: 800px;
  margin: auto auto 20px;
  font-size: 20px;
  font-style: italic;
}

.directions {
  margin-bottom: 20px;
}

